$primary: #9c27b0;

$light: #fff;
$grey-50: #fafafa;

body {
    font-family: 'Roboto', sans-serif;
    font-weight: 400;
}

.app {
    display: flex;
    flex-direction: row;
    min-height: 100vh;

    .app__page {
        width: calc(100% - 12rem);
        background: $grey-50;
        box-shadow: 0 12px 20px rgba(0, 0, 0, .2);
    }

}